/* **************** CARD FORM ****************** */
.card-form {
  border-radius: 0;
  padding: 20px 20px 0; }

.reg-form .row {
  margin-bottom: 0; }

.reg-form .textarea {
  display: block;
  font-family: 'Roboto',Arial,sans-serif;
  height: auto;
  min-height: 80px;
  resize: vertical; }

.reg-form .sbmt-btn {
  border-radius: 0;
  color: #77deef;
  text-transform: none;
  position: absolute;
  bottom: 0;
  right: .75rem; }

.input-field input.input:focus {
  background: #fff; }

.input-field input.error {
  background: #f2dedf;
  border-color: #c99ea5; }
  .input-field input.error:focus {
    background: #fff; }
  .input-field input.error ~ .input-label {
    display: none; }

.input-field label.error {
  padding-left: 15px; }
